export default {
	name:"zhuanti",
	data(){
		return {
			banner:[],
			album:[],
			params:{
				limit:10,
				order:"new",
				skip:0				
			},
			hasmore:true
		}
	},
	methods:{
		zhuanti(){
			uni.showLoading({
				title:"加载中",
				mask:true
			})
			uni.request({
				
				
				



				

				url:"http://157.122.54.189:9088/image/v1/wallpaper/album",

				
				
				
				
				data:this.params,
				success: (result) => {
					//console.log(result.data.res.album);
					if(result.data.res.album == 0){
						this.hasmore = false;
						return;
					}
					if(this.banner.length == 0){
						this.banner = result.data.res.banner;
					}
					
					this.album = [...this.album,...result.data.res.album];
					
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		scrolltolower() {
			if(this.hasmore){
				this.params.skip += this.params.limit;
				this.zhuanti();				
			}else{
				uni.showToast({
					title:"数据没有了",
					icon:"none"
				})
			}

		},
		tozhuantidetails(id){
			uni.navigateTo({
				url:"../zhantiDetails/zhantiDetails?id="+id,
				success: () => {
					
				}
			})
		}
	},
	mounted(){
		this.zhuanti();
		uni.setNavigationBarTitle({
		    title: '专题'
		});			
	}
}
